import ajax from '@/api/base';
import axios from 'axios';

let postService = axios.create({
    method: 'POST',
    baseURL: '/api/'
})

export default {
    calculationShouldPayment: opts => {
        return postService({
            url: '/open/partyMemberPayment/calculationShouldPayment',
            params: {
                ...opts
            }
        });
    },

    submit: opts => {
        return postService({
            url: '/open/partyMemberPayment/submit',
            params: {
                ...opts
            }
        });
    }
};