
import { mapGetters, mapActions } from "vuex";
import steps from "@/component/steps.vue";
import Toast from 'vux/src/components/toast/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import api from "@/api/party-member";

export default {
components: {
steps,
Datetime,
Toast
},
name: "selfRatingGrade",
data() {
return {
textNull: "",
afterTaxSalary: "",
startDate: "",
endDate: "",
shouldPayment: "0",
showToast: false,
format: "YYYY年MM月",
showPopup1: false,
showPopup2: false,
message: "请将党费交至组织关系所在的党组织",
stepsArr: [
{ label: "已确认金额", active: true },
{ label: "缴纳中", active: false },
{ label: "已缴纳", addlabel: "至律协党委", active: false }
],
validate: function() {
if (this.startDate === "") {
this.showPopup1 = true;
return false;
} else if (this.endDate === "") {
this.showPopup2 = true;
return false;
} else {
return true;
}
}
};
},
methods: {
...mapActions([]),

displayMsg() {
if (this.validate) {
}
if (this.startDate === "") {
this.showPopup1 = true;
} else if (this.endDate === "") {
this.showPopup2 = true;
} else {
this.showToast = true;
}
},
calculationShouldPayment() {
if (this.validate()) {
api
.calculationShouldPayment({
afterTaxSalary: this.afterTaxSalary,
startDate: this.startDate+"1日",
endDate: this.endDate+"1日"
})
.then(response => {
let data = response.data;
let code = data.code;
if (code === 1) {
this.shouldPayment = data.result;
} else {
this.message = data.result;
this.showToast = true;
}
});
}
},
submit() {
if (this.validate()) {
api
.submit({
afterTaxSalary: this.afterTaxSalary,
startDate: this.startDate+"1日",
endDate: this.endDate+"1日"
})
.then(response => {
let data = response.data;
let code = data.code;
if (code === 1) {
this.message = "请将党费交至组织关系所在的党组织";
} else {
console.log(data.description);
this.message = data.description;
}
this.showToast = true;
});
}
}
},
watch: {},
computed: {
...mapGetters([])
},
created() {},
mounted() {
let elem = document.querySelector("#vux_view_box_body");
setTimeout(() => {
elem.style.paddingBottom = "0";
}, 10);
}
};
